<script>
export default {
  data() {
    return {
      totalcount: 100,
      queryInfo: {
        PageNum: 1,
        PageSize: 8,
        Category_id: 0,
      },
      intro: "",
      blogList: [],
      typeList: [],
      tagList: [],
      recommendList: [],
      selectMethod: "全部博客",
      typeId: -1,
      tagId: -1,
      selected: false,
      moreType: true,
      moreTag: true,
      value: new Date(),
      timer: null,
      start: false,
      screenWidth: document.documentElement.clientWidth, //实时屏幕宽度
    };
  },
  computed: {
    pagSmall() {
      return this.screenWidth <= 768;
    },
    // 计算分页栏样式
    pagLayout() {
      if (this.screenWidth < 768) {
        return "prev, pager, next";
      } else {
        return "total, prev, pager, next, jumper";
      }
    },
  },
  created() {
    window.addEventListener("resize", this.screenAdapter);
  },
  mounted() {
    this.getTypeList();
    this.getBlogList();
    this.getTagList();
    this.getRecommendList();
    this.screenWidth = document.documentElement.clientWidth;
  },
  methods: {
    // 开始进入主页
    startRead() {
      this.$nextTick(() => {
        document.getElementById("index").scrollIntoView({
          behavior: "smooth",
          block: "start",
          // inline: 'nearest'
        });
      });
    },
    compare(property) {
      return function (a, b) {
        let value1 = a[property].length;
        let value2 = b[property].length;
        return value2 - value1;
      };
    },
    // 获取推荐博客列表
    async getRecommendList() {
      const { data: res } = await this.$blog.get("/article/newList");
      // console.log(res)
      this.recommendList = res.data;
      //this.total = res.total
    },
    // 获取博客类型列表
    async getTypeList() {
      const { data: res } = await this.$blog.get("/article/CategoryList/5");
      // console.log(res)
      this.typeList = res.data;
    },
    // 获取博客标签列表
    async getTagList() {
      const { data: res } = await this.$blog.get("/tag/getPart");
      // console.log(res)
      this.tagList = res.data;
    },
    // 获取博客列表
    async getBlogList() {
      const { data: res } = await this.$blog.get("/article/list", {
        params: this.queryInfo,
      });
      debugger
      // console.log(res)
      this.blogList = res.data.result;
      this.totalcount = res.data.totalNum;
    },
    // 跳转到博客详情页
    getBlogInfo(blogId) {
      // console.log(blogId.id)
      this.$router.push({ path: "/blogInfo", query: { id: blogId } });
      // const { href } = this.$router.resolve({
      //   path: "/blogInfo",
      //   query: {
      //     id: blogId
      //   }
      // });
      // window.open(href, "_blank");
    },
    // 修改当前页码
    handleCurrentChange(newSize) {
      this.queryInfo.PageNum = newSize;
      this.getBlogList();
    },
    // 修改当前页大小
    handleSizeChange(newSize) {
      this.queryInfo.PageSize = newSize;
    },
    // 按分类筛选博客
    async selectType(id) {
      this.typeId = id;
      this.queryInfo.Category_id = id;
      const { data: res } = await this.$blog.get("/article/list", {
        params: this.queryInfo,
      });
      // console.log(res)
      this.blogList = res.data.result;
      this.totalcount = res.data.totalNum;
      this.selectMethod =
        "分类: " +
        this.typeList.find((item) => item.category_Id === this.typeId).name;
      this.selected = true;
    },
    // 按标签筛选博客
    async selectTag(id) {
      this.tagId = id;
      const { data: res } = await this.$blog.get(`/tag/getBlogs/${this.tagId}`);
      this.blogList = res.data;
      this.totalcount = res.data.length;
      this.selectMethod =
        "标签: " + this.tagList.find((item) => item.id === this.tagId).name;
      this.selected = true;
    },
    // 更新博客列表
    updateBlogList() {
      this.selected = false;
      this.typeId = -1;
      this.tagId = -1;
      this.getBlogList();
    },
    // 得到所有的分类
    async getFullTypeList() {
      const { data: res } = await this.$blog.get("/article/CategoryList/0");
      this.typeList = res.data;
    },
    // 得到所有的标签
    async getFullTagList() {
      const { data: res } = await this.$blog.get("/tag/getList");
      // console.log(res.data)
      this.tagList = res.data;
    },
    async dealType() {
      if (this.moreType) {
        await this.getFullTypeList();
      } else {
        await this.getTypeList();
      }
      this.moreType = !this.moreType;
    },
    async dealTag() {
      if (this.moreTag) {
        await this.getFullTagList();
      } else {
        await this.getTagList();
      }
      this.moreTag = !this.moreTag;
    },
    // 屏幕尺寸变化的监听函数
    screenAdapter() {
      this.screenWidth = document.documentElement.clientWidth;
    },
  },
};
</script>

